	module.exports = {"data":{"markdownRemark":{"html":"<h3>Reputation on Triage</h3>\n<p>No need to wait for reports to be resolved in order to increase reputation! We now enable hackers to gain reputation whenever their reports are marked as <i>Triaged</i>. </p>\n<h3>Bounty Tables</h3>\n<p>Instead of having programs manually create their own bounty table on the policy page using tedious markdown, we now enable them to easily generate their own bounty table with our new <a href=\"https://docs.hackerone.com/programs/bounty-tables.html\">bounty table</a> tool. </p>","frontmatter":{"path":"/changelog/2018/june","title":"June 2018"}}},"pathContext":{}}

